import { Connection, PublicKey, AddressLookupTableAccount } from "@solana/web3.js";
import { getMultipleAccountsInfo } from "../accountInfo";

export interface CacheLTA {
  [key: string]: AddressLookupTableAccount;
}

export async function getMultipleLookupTableInfo({
  connection,
  address,
  cluster = "mainnet",
}: {
  connection: Connection;
  address: PublicKey[];
  cluster?: "mainnet" | "devnet";
}): Promise<CacheLTA> {
  const dataInfos = await getMultipleAccountsInfo(
    connection,
    [...new Set<string>(address.map((i) => i.toString()))].map((i) => new PublicKey(i)),
  );

  const outDict: CacheLTA = {};
  for (let i = 0; i < address.length; i++) {
    const info = dataInfos[i];
    const key = address[i];
    if (!info) continue;
    const lookupAccount = new AddressLookupTableAccount({
      key,
      state: AddressLookupTableAccount.deserialize(info.data),
    });
    outDict[key.toString()] = lookupAccount;

    if (cluster === "devnet") DEV_LOOKUP_TABLE_CACHE[key.toString()] = lookupAccount;
    else LOOKUP_TABLE_CACHE[key.toString()] = lookupAccount;
  }

  return outDict;
}

export const LOOKUP_TABLE_CACHE: CacheLTA = {
  AcL1Vo8oy1ULiavEcjSUcwfBSForXMudcZvDZy5nzJkU: new AddressLookupTableAccount({
    key: new PublicKey("AcL1Vo8oy1ULiavEcjSUcwfBSForXMudcZvDZy5nzJkU"),
    state: AddressLookupTableAccount.deserialize(
      Buffer.from(
        "",
        "base64",
      ),
    ),
  }),
};

export const DEV_LOOKUP_TABLE_CACHE: CacheLTA = {};
export const getDevLookupTableCache = async (connection: Connection) => {
  const devAltStr = "EFhMuDw1PKEuckuFRW9PavNfTH4LKP5uKHgyXDmWpFCq";
  if (DEV_LOOKUP_TABLE_CACHE[devAltStr]) return DEV_LOOKUP_TABLE_CACHE;

  const devAlt = new PublicKey(devAltStr);
  const data = await connection.getAccountInfo(devAlt);
  if (!data) return DEV_LOOKUP_TABLE_CACHE;
  DEV_LOOKUP_TABLE_CACHE[devAltStr] = new AddressLookupTableAccount({
    key: devAlt,
    state: AddressLookupTableAccount.deserialize(data.data),
  });

  return DEV_LOOKUP_TABLE_CACHE;
};
